<template>
  <div>
    <div class="background-grey"></div>
    <div class="model">
      <p></p>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
        <el-form-item label="?" prop="isMedicalInsuranceCard">
          <el-switch v-model="ruleForm.isMedicalInsuranceCard"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="edicalInsuranceCard" v-if="ruleForm.isMedicalInsuranceCard">
          <el-input v-model="ruleForm.medicalInsuranceCard"></el-input>
        </el-form-item>
        <el-form-item label="" prop="medicalCard" v-else class="medicalCard">
          <el-input v-model="ruleForm.medicalCard"></el-input>
        </el-form-item>
        <el-form-item label="?" prop="sex">
          <el-switch v-model="ruleForm.sex"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="allergiHistory">
          <el-input v-model="ruleForm.allergiHistory"></el-input>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input v-model="ruleForm.detail"></el-input>
        </el-form-item>
        <el-form-item label="?" prop="fever">
          <el-switch v-model="ruleForm.fever"></el-switch>
        </el-form-item>
      </el-form>
      <el-button style="margin-top: 10px" @click="cancleModel"></el-button>
      <el-button type="primary" @click="order('ruleForm')"></el-button>
    </div>
  </div>
</template>
<script>
export default {
  props: ['date', 'time', 'doctorMessage', 'signalSource'],
  // inject: ['reload'],
  created() {
    let userId = localStorage.getItem('userId')
    this.$axios.post('http://localhost:3000/api/Stu/showOfId', { userId }).then(response => {
      this.userMessage = response.data[0]
      console.log(response.data)
    })
  },
  data() {
    return {
      ruleForm: {
        allergiHistory: '',
        medicalInsuranceCard: '',
        medicalCard: '',
        isMedicalInsuranceCard: false,
        detail: '',
        fever: false,
        sex: false
      },
      rules: {
        allergiHistory: { required: true, message: '', trigger: 'blur' },
        medicalInsuranceCard: { message: '', trigger: 'blur' },
        medicalCard: { message: '', trigger: 'blur' }
      },
      userMessage: []
    }
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    cancleModel() {
      this.$emit('cancle', false)
    },
    order(formName) {
      if (this.ruleForm.fever) {
        this.$message({
          type: 'info',
          message: ','
        })
      } else {
        this.$refs[formName].validate(valid => {
          console.log(valid, 'valid')
          if (valid) {
            if (this.ruleForm.isMedicalInsuranceCard) {
              if (this.userMessage.edicalInsurance_card) {
                if (this.userMessage.edicalInsurance_card === parseInt(this.ruleForm.medicalInsuranceCard)) {
                  this.addOrder()
                } else {
                  this.$message({
                    type: 'info',
                    message: ''
                  })
                }
              } else {
                this.$message({
                  type: 'info',
                  message: ','
                })
              }
            } else {
              if (this.userMessage.medical_card) {
                if (this.userMessage.medical_card === parseInt(this.ruleForm.medicalCard)) {
                  this.addOrder()
                } else {
                  this.$message({
                    type: 'info',
                    message: ''
                  })
                }
              } else {
                this.$message({
                  type: 'info',
                  message: ','
                })
              }
            }
          } else {
            this.$message({
              type: 'info',
              message: ''
            })
          }
        })
      }
    },
    addOrder() {
      // 
      if (this.date) {
        let date = new Date(this.date)
        let time = new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} ${this.time}`).getTime()
        let userId = localStorage.getItem('userId')
        let params
        if (this.ruleForm.isMedicalInsuranceCard) {
          params = {
            userId,
            allergiHistory: this.ruleForm.allergiHistory,
            medicalInsuranceCard: parseInt(this.ruleForm.medicalInsuranceCard),
            detail: this.ruleForm.detail,
            time,
            doctorId: this.doctorMessage.id,
            doctorName: this.doctorMessage.name,
            part: this.doctorMessage.part,
            sex: this.ruleForm.sex ? '' : ''
          }
        } else {
          params = {
            userId,
            allergiHistory: this.ruleForm.allergiHistory,
            medicalCard: parseInt(this.ruleForm.medicalCard),
            detail: this.ruleForm.detail,
            time,
            doctorId: this.doctorMessage.id,
            doctorName: this.doctorMessage.name,
            part: this.doctorMessage.part,
            sex: this.ruleForm.sex ? '' : ''
          }
        }

        this.$axios.post('http://localhost:3000/api/Stu/addOrder', params).then(response => {
          if (response.status === 200) {
            this.$axios
              .post('http://localhost:3000/api/Stu/substrSignalSource', { signalSource: this.signalSource - 1, doctorId: this.doctorMessage.id, date: this.date })
              .then(res => {
                console.log(res, '')
                if (typeof res.data === 'string') {
                  this.$message({
                    type: 'info',
                    message: res.data
                  })
                } else if (res.status === 200) {
                  this.$message({
                    message: ',',
                    type: 'success'
                  })
                  // this.reload()
                }
              })
              .catch(err => console.log(err))
          }
        })
      } else {
        this.$emit('isOnlie', this.ruleForm)
      }
    }
  }
}
</script>
<style scoped>
/* .background-grey {
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: grey;
  top: 0;
  opacity: 0.5;
} */
.model {
  z-index: 111;
  padding: 10px;
  position: absolute;
  background: white;
  top: 300px;
  left: 600px;
  width: 376px;
  margin-top: -200px;
}
.click-Time {
  color: aqua;
}
</style>
<style>
div .model .el-form-item__content {
  width: 200px;
}
div .model form.el-form.demo-ruleForm {
  margin-top: 0;
}
</style>
