<template>
  <div style="margin-top: -200px" class="sf">
    <p></p>
    <el-form ref="Form" :rules="rules" status-icon :model="edit" label-width="100px">
      <el-form-item label="" prop="id_number">
        <el-input v-model="edit.id_number"></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="edit.name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="$emit('cancle', true)"></el-button>
        <el-button @click="resetForm('Form')"></el-button>
        <el-button type="primary" @click="submitForm('Form')"></el-button>
      </el-form-item>
    </el-form>
  </div>
  <!--  -->
  <!--  -->
</template>
<script>
import { validateSfz, validateRealName } from './validateSfz.js'
export default {
  data() {
    return {
      edit: {
        id_number: '',
        name
      },
      rules: {
        id_number: [{ validator: validateSfz, trigger: 'blur' }],
        name: [{ validator: validateRealName, trigger: 'blur' }]
      },
      text: ''
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$emit('authentication', this.edit)
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>
<style scoped>
div.sf {
  background: white;
  z-index: 111;
  position: absolute;
  width: 500px;
  padding: 20px;
}
</style>
