<template>
  <div class="doctor-manage" style="display: flex; margin-top: 20px">
    <el-tree ref="tree" :data="treeData" :props="defaultProps" @node-click="handleNodeClick" node-key="id" style="width: 10%"></el-tree>
    <el-form ref="form" v-if="selectData.detail || selectData.name" :rules="rules" :model="selectData">
      <el-form-item label="" label-width="80px" prop="name">
        <el-input v-model="selectData.name" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="" label-width="80px" prop="title">
        <el-select v-model="selectData.title">
          <el-option label="" value=""> </el-option>
          <el-option label="" value=""> </el-option>
          <el-option label="" value=""> </el-option>
          <el-option label="" value=""> </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" label-width="80px" prop="part">
        <el-select v-model="selectData.part">
          <el-option v-for="partOne in schedule" :key="partOne.part" :value="partOne.part" :label="partOne.part"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" label-width="80px" prop="sex">
        <el-select v-model="selectData.sex">
          <el-option value="" label=""></el-option>
          <el-option value="" label=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" label-width="80px" prop="email">
        <el-input v-model="selectData.email"></el-input>
      </el-form-item>
      <el-form-item label="" label-width="80px" prop="phone">
        <el-input v-model="selectData.phone"></el-input>
      </el-form-item>
      <el-form-item label="" label-width="80px" prop="detail">
        <el-input type="textarea" v-model="selectData.detail" autosize></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="edit"></el-button>
        <el-button @click="dialogVisible = true"></el-button>
        <el-popconfirm title="" @confirm="deteleDoctor">
          <el-button slot="reference"></el-button>
        </el-popconfirm>
      </el-form-item>
    </el-form>
    <div class="image" v-if="selectData.detail || selectData.name">
      <el-upload action="#" list-type="picture-card" :auto-upload="false" :limit="1" :on-change="changeImage">
        <img class="el-upload-list__item-thumbnail" :src="selectData.image" alt="" />
      </el-upload>
    </div>
    <el-dialog title="" :visible.sync="dialogVisible" :before-close="handleClose">
      <RegisterDoctor :isManage="true" @getForm="getAddForm" :confim="confim"></RegisterDoctor>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose"> </el-button>
        <el-button type="primary" @click="addForm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { schedule } from '../part'
import RegisterDoctor from '../RegisterDoctor.vue'
export default {
  components: {
    RegisterDoctor
  },
  data() {
    return {
      addFormData: [],
      dialogVisible: false,
      schedule: schedule,
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      treeData: [],
      confim: false,
      selectData: {
        phone: null,
        name: null,
        email: null,
        image: null,
        part: null,
        title: null,
        detail: null
      },
      rules: {
        phone: { required: true, message: '', trigger: 'blur' },
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: ['blur', 'change'] }
        ],
        image: { required: true, message: '', trigger: 'blur' },
        part: { required: true, message: '', trigger: 'blur' },
        title: { required: true, message: '', trigger: 'blur' },
        detail: { required: true, message: '', trigger: 'blur' }
      }
    }
  },
  created() {
    this.getDoctor()
  },
  methods: {
    addForm() {
      this.$axios
        .post('http://localhost:3000/api/Stu/addDoctorUser', {
          ...this.addFormData
        })
        .then(response => {
          console.log(response, 'response')
          if (response.status === 200) {
            this.dialogVisible = false
            this.addFormData = {}
            this.$message.success('')
            this.selectData = {}
            this.getDoctor()
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    getAddForm(form) {
      this.addFormData = form
    },
    handleClose() {
      this.dialogVisible = false
    },
    getDoctor() {
      this.$axios
        .get('http://localhost:3000/api/Manage/searchDoctorDoctorUser')
        .then(res => {
          let partObj = {}
          schedule.forEach(part => {
            partObj[part.part] = part.part
          })
          let treeData = {}
          res.data.forEach(item => {
            if (partObj[item.part]) {
              if (!treeData[item.part]) {
                treeData[item.part] = {
                  label: '',
                  children: []
                }
              }
              treeData[item.part].children.push({ label: item.name, ...item })
              treeData[item.part].label = item.part
            }
          })
          this.treeData = Object.values(treeData)
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    deteleDoctor() {
      this.$axios
        .post('http://localhost:3000/api/Manage/deleteDoctor', { id: this.selectData.id })
        .then(res => {
          if (res.status === 200) this.$message.success('')
          this.getDoctor()
          this.selectData = {}
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    edit() {
      this.$refs['form'].validate(valid => {
        console.log(valid, this.rules, this.selectData)
        if (valid) {
          console.log(this.selectData)
          this.$axios
            .post('http://localhost:3000/api/Manage/editDoctor', { ...this.selectData })
            .then(res => {
              if (res.status === 200) this.$message.success('')
            })
            .catch(err => {
              this.$message.error(err)
            })
        } else {
          this.$message.error('')
          return false
        }
      })
    },
    handleNodeClick(data) {
      this.selectData = data
    },
    changeImage(file) {
      console.log(file, 'file res')
      //blobbase64
      let that = this
      this.blobToDataURI(file.raw, function (data) {
        that.selectData.image = data
      })
    },
    blobToDataURI(blob, callback) {
      var reader = new FileReader()
      reader.onload = function (e) {
        callback(e.target.result)
      }
      reader.readAsDataURL(blob)
    }
  }
}
</script>
<style>
.doctor-manage .el-form-item__content {
  width: 600px;
}
.doctor-manage .el-input {
  width: 600px;
}
.doctor-manage img {
  max-width: 170px;
}
</style>
<style scoped>
img {
  max-width: 170px;
}
.image {
  width: 170px;
  height: 100px;
  overflow: hidden;
  margin-left: 80px;
}
</style>
