<template>
  <div class="history" v-if="historyTalk.length > 0">
    <p></p>
    <el-table :data="historyTalk" style="width: 100%">
      <el-table-column prop="userName" label="" width="180"> </el-table-column>
      <el-table-column prop="isPrescription" label="" width="180"> </el-table-column>
      <el-table-column prop="startTime" label="" width="180"> </el-table-column>
      <el-table-column prop="endTime" label="" width="180"> </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
          <el-button @click="detail(scope.$index)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="background-grey" v-if="dialogVisible"></div>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-table :data="selectTalk">
        <el-table-column prop="talkFrom" label=""> </el-table-column>
        <el-table-column prop="talkTo" label=""> </el-table-column>
        <el-table-column prop="text" label=""> </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      historyTalk: [],
      userId: '',
      dialogVisible: false,
      selectTalk: []
    }
  },
  created() {
    this.userId = localStorage.getItem('userId')
    this.$axios.post('http://localhost:3000/api/Stu/searchHistoryTalk', { doctorId: this.userId }).then(response => {
      if (response.status === 200) {
        this.historyTalk = response.data.map(item => {
          item.isPrescription = item.isPrescription !== '0' ? '' : ''
          item.startTime = `${new Date(item.startTime).getFullYear()}-${new Date(item.startTime).getMonth() + 1}-${new Date(item.startTime).getDate()}`
          item.endTime = `${new Date(item.endTime).getFullYear()}-${new Date(item.endTime).getMonth() + 1}-${new Date(item.endTime).getDate()}`
          return item
        })
      }
    })
  },
  methods: {
    detail(index) {
      this.selectTalk = JSON.parse(this.historyTalk[index].info)
      this.dialogVisible = true
    }
  }
}
</script>
