<template>
  <div class="triangle">
    <header class="tri-header">
      <h2 class="tri-title"></h2>
    </header>
    <main>
      <div class="operator">
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm">
          <el-form-item prop="graphical" label="">
            <el-radio-group v-model="ruleForm.graphical">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            prop="direction"
            label=""
            v-if="ruleForm.graphical === ''"
          >
            <el-radio-group v-model="ruleForm.direction">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            prop="width"
            label=":"
            v-if="hasWHD.includes(ruleForm.graphical)"
          >
            <el-slider v-model="ruleForm.width" :max="200"></el-slider>
          </el-form-item>
          <el-form-item
            prop="height"
            label=":"
            v-if="hasWHD.includes(ruleForm.graphical)"
          >
            <el-slider v-model="ruleForm.height" :max="200"></el-slider>
          </el-form-item>
          <el-form-item prop="rotate" label=":">
            <el-slider v-model="ruleForm.rotate" :max="360"></el-slider>
          </el-form-item>
          <el-form-item prop="color" label=":">
            <el-input type="color" v-model="ruleForm.color"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="display-true">
        <div
          class="samll-triangle"
          v-if="ruleForm.graphical === ''"
          :style="{
            borderColor: getBorderColor,
            borderWidth: getBorderWidth,
            transform: 'rotate(' + ruleForm.rotate + 'deg)',
          }"
        ></div>
        <div
          id="star-five"
          v-if="ruleForm.graphical === ''"
          :style="getStarCss['#star-five']"
        >
          <div class="star-before" :style="getStarCss['.star-before']"></div>
          <div class="star-after" :style="getStarCss['.star-after']"></div>
        </div>
        <div
          id="parallelogram"
          v-if="ruleForm.graphical === ''"
          :style="` width: ${ruleForm.width}px; height: ${ruleForm.height}px; transform: skew(20deg) rotate(${ruleForm.rotate}deg); background: ${ruleForm.color};`"
        ></div>
        <div
          id="trapezoid"
          v-if="ruleForm.graphical === ''"
          :style="csstrapezoid"
        ></div>
      </div>
      <div class="copy-css operator m-t-10">
        <header class="tri-header flex">
          <h3 class="tri-title">css</h3>
          <el-button @click="copy"></el-button>
        </header>
        <el-input
          v-if="ruleForm.graphical === ''"
          type="textarea"
          disabled
          v-model="getCss"
          :rows="7"
          resize="none"
        ></el-input>
        <el-input
          v-if="ruleForm.graphical === ''"
          type="textarea"
          disabled
          v-model="getStarCssAll"
          :rows="14"
          resize="none"
        ></el-input>
        <el-input
          v-if="ruleForm.graphical === ''"
          v-model="cssparallelogram"
          disabled
          :rows="14"
          resize="none"
        ></el-input>

        <el-input
          v-if="ruleForm.graphical === ''"
          v-model="csstrapezoid"
          disabled
          :rows="6"
          resize="none"
          type="textarea"

        ></el-input>
      </div>
      <div class="copy-html m-t-10" v-if="ruleForm.graphical === ''">
        <header class="tri-header flex">
          <h3 class="tri-title">html</h3>
          <el-button @click="copyHtml"></el-button>
        </header>

        <el-input
          type="textarea"
          disabled
          :value="getStarHtml"
          :rows="1"
          resize="none"
        ></el-input>
      </div>
    </main>
  </div>
</template>
<script>
export default {
  components: {

  },
  data() {
    return {
      ruleForm: {

        direction: '',
        width: 60,
        height: 60,
        rotate: 0,
        color: 'rgb(0, 0, 0)',
        graphical: ''
      },

      hasWHD: ['', '', ''],
      rules: {},
    }
  },
  computed: {
    csstrapezoid() {
      return `border-bottom: ${this.ruleForm.height}px solid ${this.ruleForm.color};
      border-left: ${this.ruleForm.width / 4}px solid transparent;
      border-right: ${this.ruleForm.width / 4}px solid transparent;
      height: 0;
      width: ${this.ruleForm.width / 2}px;
      transform:rotate(${this.ruleForm.rotate}deg);`
    },
    cssparallelogram() {
      return `width: ${this.ruleForm.width}px; height: ${this.ruleForm.height}px; transform: skew(20deg) rotate(${this.ruleForm.rotate}deg); background: ${this.ruleForm.color};`
    },
    getStarHtml() {
      return `<div id="star-five"><div class="star-before" ></div><div class="star-after" ></div></div>`
    },
    getBorderColor() {
      const temp = {
        '': 'transparent transparent ' + this.ruleForm.color,
        '': this.ruleForm.color + ' transparent transparent ',
        '': 'transparent ' + this.ruleForm.color + ' transparent transparent ',
        '': 'transparent  transparent transparent ' + this.ruleForm.color,

      }
      return temp[this.ruleForm.direction]
    },
    getBorderWidth() {
      const temp = {
        '': `0px ${this.ruleForm.width / 2}px ${this.ruleForm.height}px`,
        '': `${this.ruleForm.height}px ${this.ruleForm.width / 2}px 0px`,
        '': `${this.ruleForm.height / 2}px ${this.ruleForm.width}px  ${this.ruleForm.height / 2}px 0px`,
        '': `${this.ruleForm.height / 2}px 0px ${this.ruleForm.height / 2}px ${this.ruleForm.width}px `,
      }
      return temp[this.ruleForm.direction]
    },
    getCss() {
      return ` border-color: ${this.getBorderColor};
 border-width: ${this.getBorderWidth};
 transform: rotate(${this.ruleForm.rotate}deg);
 display: inline-block;
 border-style: solid;
 width: 0;
 height: 0;`

    },
    getStarCss() {
      return {
        '#star-five': `margin: 100px 0;
position: relative;
display: block;
color:  ${this.ruleForm.color};
width: 0px;
height: 0px;
border-right: 100px solid transparent;
border-bottom: 70px solid ${this.ruleForm.color};
border-left: 100px solid transparent;
transform: rotate(${this.ruleForm.rotate + 35}deg);`,
        '.star-before': `border-bottom: 80px solid  ${this.ruleForm.color};
border-left: 30px solid transparent;
border-right: 30px solid transparent;
position: absolute;
height: 0;
width: 0;
top: -45px;
left: -65px;
transform: rotate(-35deg);`,
        '.star-after': `position: absolute;
color:   ${this.ruleForm.color};
top: 3px;
left: -105px;
width: 0px;
height: 0px;
border-right: 100px solid transparent;
border-bottom: 70px solid   ${this.ruleForm.color};
border-left: 100px solid transparent;
transform: rotate(-70deg);`

      }
    },
    getStarCssAll() {
      return `#star-five{
        ${this.getStarCss['#star-five']}
      }
      .star-before{
        ${this.getStarCss['.star-before']}
      }
      .star-after{
        ${this.getStarCss['.star-after']}
      }
      `
    }
  },
  methods: {
    copy() {
      const temp = { '': this.getCss, '': this.getStarCssAll, '': this.cssparallelogram ,'':this.csstrapezoid}
      navigator.clipboard.writeText(temp[this.ruleForm.graphical])
      this.$message.success('')
    },
    copyHtml() {
      navigator.clipboard.writeText(this.getStarHtml)
      this.$message.success('')
    }
  }
}
</script>
<style lang="scss" scoped>
.triangle {
  background: #dbdbdb;
  height: 100%;
  width: 100%;
  margin-top: -20px;
  padding: 20px;
  box-sizing: border-box;
  main {
    display: flex;
    flex-wrap: wrap;
    .operator {
      width: calc(50% - 5px);
      // height: 500px;
      background: rgb(255, 255, 255);
      margin-right: 10px;
      padding: 10px;
      box-sizing: border-box;
    }
    .copy-html {
      width: calc(50% - 5px);
      // height: 500px;
      background: rgb(255, 255, 255);
      padding: 10px;
      box-sizing: border-box;
      .tri-header {
        justify-content: space-between;
      }
    }
    .display-true {
      width: calc(50% - 5px);
      height: 300px;
      background: rgba(255, 255, 255, 0.6);
      line-height: 300px;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      .samll-triangle {
        display: inline-block;
        border-style: solid;
        width: 0;
        height: 0;
      }
    }
    .copy-css {
      .tri-header {
        justify-content: space-between;
      }
    }
  }
}
</style>