<template>
  <div class="game">
    <div class="game-main">
      <div class="game-div">
        <div v-for="(box, index) in boxs" :key="index" class="flex">
          <div v-for="(item, index2) in box" :key="index2" class="box" :style="item.num!==0?'color:white;background:green;':''">
            {{ item.num }}
          </div>
        </div>
      </div>
    </div>
    <p>
      :
      6*62
      4 
      2048
    </p>
    <dialog-vue dialogTitle="" v-model:visible="visible">
      <template v-slot:body>
        {{ tip }}
      </template>
      <template v-slot:footer>
        <el-button @click="init"></el-button>
      </template>
    </dialog-vue>
  </div>
</template>
<script >

import { ref } from 'vue'
import dialogVue from "../comm/dialog.vue";
export default {
  components: { dialogVue },
  setup() {
    const row = ref(6)//
    const col = ref(6)//
    const boxs = ref([[{ x: 0, y: 0, num: 0 }]])
    const copyBoxs = ref([])
    const visible = ref(false)
    const tip = ref('')
    function init() {
      visible.value=false
      boxs.value = []
      for (let i = 0; i < row.value; i++) {

        boxs.value.push([])
        for (let j = 0; j < col.value; j++) {
          copyBoxs.value.push(i * row.value + j)
          boxs.value[i].push({ x: i, y: i, num: 0 })
        }
      }
      boxs.value[Math.floor(Math.random() * col.value)][Math.floor(Math.random() * row.value)].num = 2
    }
    init()
    keyDown()
    function keyDown() {
      document.onkeydown = (e) => {
        //
        let e1 = e || arguments.callee.caller.arguments[0]
        //:-LEFT_ARROW ;-UP_ARROW -RIGHT_ARROW ;-DOWN_ARROW//
        if (e1 && e1.key == 'ArrowLeft') {// 
          moveLeft()
        } else if (e1 && e1.key == 'ArrowRight') {// 
          moveRight()
        } else if (e1 && e1.key === 'ArrowUp') {
          moveUp()
        } else if (e1 && e1.key === 'ArrowDown') {
          moveDown()
        }
      }
    }
    function moveLeft() {
      let hasRandomNum = false
      let num = 0
      for (let i = 0; i < row.value; i++) {
        for (let j = 1; j < col.value; j++) {

          if (boxs.value[i][j].num > 0) {
            if (boxs.value[i][j - 1].num === 0) {
              boxs.value[i][j - 1].num = boxs.value[i][j].num
              boxs.value[i][j].num = 0

            } else if (boxs.value[i][j - 1].num === boxs.value[i][j].num) {

              boxs.value[i][j - 1].num = boxs.value[i][j].num * 2
              boxs.value[i][j].num = 0
              if (boxs.value[i][j - 1].num >= 2048) {
                i = row.value
                j = col.value
                tip.value = ''
                visible.value = true
                break
              }
            }
          } else {
            if (Math.random() > 0.5 && !hasRandomNum) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              hasRandomNum = true
            }
          }
        }
      }
      
      if (!hasRandomNum) {
        for (let i = 0; i < row.value; i++) {
          for (let j = 0; j < col.value; j++) {
            if (boxs.value[i][j].num === 0) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              i = row.value
              j = col.value
              break
            }else{
              num++
            }
          }
        }
      }
       if (num >= row.value * col.value) {
        tip.value = ''
        visible.value = true
        return
      }
    }
    function moveRight() {
      let hasRandomNum = false
      let num = 0

      for (let i = 0; i < row.value; i++) {
        for (let j = col.value - 2; j >= 0; j--) {
          if (boxs.value[i][j].num > 0) {
            if (boxs.value[i][j + 1].num === 0) {
              boxs.value[i][j + 1].num = boxs.value[i][j].num
              boxs.value[i][j].num = 0
            } else if (boxs.value[i][j + 1].num === boxs.value[i][j].num) {
              boxs.value[i][j + 1].num = boxs.value[i][j].num * 2
              boxs.value[i][j].num = 0
              if (boxs.value[i][j + 1].num >= 2048) {
                i = row.value
                j = col.value
                tip.value = ''
                visible.value = true
                break
              }
            }
          } else {
            if (Math.random() > 0.5 && !hasRandomNum) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              hasRandomNum = true
            }
          }
        }
      }  
      if (!hasRandomNum) {
        for (let i = 0; i < row.value; i++) {
          for (let j = 1; j < col.value; j++) {
            if (boxs.value[i][j].num === 0) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              i = row.value
              j = col.value
              break
            }else{
              num++
            }
          }
        }
      }
       if (num >= row.value * col.value) {
        tip.value = ''
        visible.value = true
        return
      }
    }
    function moveUp() {
      let hasRandomNum = false
      let num = 0

      for (let i = 1; i < row.value; i++) {
        for (let j = 0; j < col.value; j++) {
          if (boxs.value[i][j].num > 0) {
            if (boxs.value[i - 1][j].num === 0) {
              boxs.value[i - 1][j].num = boxs.value[i][j].num
              boxs.value[i][j].num = 0
            } else if (boxs.value[i - 1][j].num === boxs.value[i][j].num) {
              boxs.value[i - 1][j].num = boxs.value[i][j].num * 2
              boxs.value[i][j].num = 0
              if (boxs.value[i - 1][j].num >= 2048) {
                i = row.value
                j = col.value
                tip.value = ''
                visible.value = true
                break
              }
            }
          } else {
            if (Math.random() > 0.5 && !hasRandomNum) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              hasRandomNum = true
            }
          }
        }
      } 
     
      if (!hasRandomNum) {
        for (let i = 0; i < row.value; i++) {
          for (let j = 0; j < col.value; j++) {
            if (boxs.value[i][j].num === 0) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              i = row.value
              j = col.value
              break
            }else{
              num++
            }
          }
        }
      }
       if (num >= row.value * col.value) {
        tip.value = ''
        visible.value = true
        return
      }
    }
    function moveDown() {
      let hasRandomNum = false
      let num = 0

      for (let i = row.value - 2; i >= 0; i--) {
        for (let j = 0; j < col.value; j++) {
          if (boxs.value[i][j].num > 0) {
            if (boxs.value[i + 1][j].num === 0) {
              boxs.value[i + 1][j].num = boxs.value[i][j].num
              boxs.value[i][j].num = 0
            } else if (boxs.value[i + 1][j].num === boxs.value[i][j].num) {
              boxs.value[i + 1][j].num = boxs.value[i][j].num * 2
              boxs.value[i][j].num = 0
              if (boxs.value[i + 1][j].num >= 2048) {
                i = row.value
                j = col.value
                tip.value = ''
                visible.value = true
                break
              }
            }
          } else {
            if (Math.random() > 0.5 && !hasRandomNum) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              hasRandomNum = true
            }
          }
        }
      }  
      if (!hasRandomNum) {
        for (let i = 0; i < row.value; i++) {
          for (let j = 0; j < col.value; j++) {
            if (boxs.value[i][j].num === 0) {
              boxs.value[i][j].num = [2, 4][Math.floor(Math.random() * 2)]
              i = row.value
              j = col.value
              break
            }else{
              num++
            }
          }
        }
      }
       if (num >= row.value * col.value) {
        tip.value = ''
        visible.value = true
        return
      }
    }
    return {
      boxs,
      init,
      visible,
      tip
    }
  }
}

</script>
<style lang="scss" scoped>
.game {
  padding: 20px;
  .game-main {
    .game-div {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 30px;
      .box {
        width: 30px;
        height: 30px;
        border: 1px solid #a7ec26;
        text-align: center;
        line-height: 30px;
        box-sizing: border-box;
        outline: none;
      }
    }
  }
}
::selection {
  // background: transparent;
  outline: none;
  // color: #ffffff;
}
</style>